  .header p {
    float: left;
    color: rgb(255, 255, 255);
    text-align: center;
    padding: 12px;
    text-decoration: none;
    font-size: 14px;
    line-height: 12px;
    border-radius: 4px;
    margin: 3px3px3px3px;
    /* margin-bottom: 2px; */
  }
img{

    margin-left: 4px;
    margin-top: 3px;
    height: 70px;
    width: 200px;
    margin-bottom: 1px;
    border-radius: 10px;
    padding: 3px;
    float: left;
}
  .header p.logo {
    font-size: 25px;
    font-weight: bold;
    color: #1369ff;
    margin: 2px2px2px2px;
  }
  
  .header p:hover {
    background-color: rgb(212, 212, 212);
    color: rgb(15, 13, 13);
  }
  
 .header p.active{
    background-color: rgb(255, 255, 255);
    color: rgb(12, 11, 11);
    /* position: relative; */

  }
 
  .header-right {
    float: right;
  }
  
  @media screen and (max-width: 500px) {
    .header p {
      float: none;
      display: block;
      text-align: left;
    }
  
    .header-right {
      float: none;
    }
  }
  